import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import withStyles from '@material-ui/styles/withStyles'

import SectionHeader from '../components/sections-header/section-header'
import PartnerCard from '../components/partner-card/partner-card'

import gdnLogo from '../images/gdn_logo.png'
import innkubatorLogo from '../images/innkubator_logo.png'
import ycLogo from '../images/yc_logo.png'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2, 0),
    margin: theme.spacing(2, 0),
  },
  partner: {
    padding: theme.spacing(0),
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(1),
    },
  },
})

const Partners = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query Partners {
          allSectionsJson(filter: { id: { eq: "partners" } }) {
            nodes {
              title
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />

            <Grid
              container
              direction={'row'}
              justify={'center'}
              alignItems={'center'}
              className={classes.content}
            >
              <Grid item sm={4} className={classes.partner}>
                <PartnerCard
                  href={
                    'https://www.gruenderzentrum-digitalisierung-niederbayern.de/'
                  }
                  altText={'GDN Logo'}
                  image={gdnLogo}
                />
              </Grid>
              <Grid item sm={4} className={classes.partner}>
                <PartnerCard
                  href={'https://www.innkubator.de/'}
                  altText={'Innkubator Logo'}
                  image={innkubatorLogo}
                />
              </Grid>
              <Grid item sm={4} className={classes.partner}>
                <PartnerCard
                  href={
                    'https://www.startupschool.org/companies/vki-fHGba2Z5rA'
                  }
                  altText={'Y Startup School'}
                  image={ycLogo}
                />
              </Grid>
            </Grid>
          </Container>
        )
      }}
    />
  )
}

Partners.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Partners)
