import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import CtaButton from '../components/cta-button/cta-button'
import SectionHeader from '../components/sections-header/section-header'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
  },
})

const ContactUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query ContactUs {
          allSectionsJson(filter: { id: { eq: "contact" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />

            <Card className={classes.content} raised={true}>
              <CardContent>
                <Typography variant={'body1'} align={'left'} component={'p'}>
                  {sectionDetails.desc}
                </Typography>
                <CtaButton />
              </CardContent>
            </Card>
          </Container>
        )
      }}
    />
  )
}

ContactUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(ContactUs)
