import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Container from '@material-ui/core/Container'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import { ChevronRight } from '@material-ui/icons'

import SectionHeader from '../components/sections-header/section-header'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
  },
  itemIcon: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
})

const WhyUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query Qualities {
          allQualitiesJson {
            nodes {
              reason
            }
          }
          allSectionsJson(filter: { id: { eq: "qualities" } }) {
            nodes {
              title
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allQualitiesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const qualities = allQualitiesJson.nodes

        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />

            <Card className={classes.content} raised={true}>
              <CardContent>
                <List>
                  {qualities.map(({ reason }, index) => (
                    <ListItem
                      key={index}
                      alignItems={'flex-start'}
                      dense={true}
                    >
                      <ListItemIcon className={classes.itemIcon}>
                        <ChevronRight className={classes.chevronRight} />
                      </ListItemIcon>
                      <ListItemText
                        primary={
                          <Typography variant={'body1'}>{reason}</Typography>
                        }
                      />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
            </Card>
          </Container>
        )
      }}
    />
  )
}

WhyUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(WhyUs)
