import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import SectionHeader from '../components/sections-header/section-header'
import Carousel from '../components/carousel/carousel'
import CarouselCard from '../components/carousel-card/carousel-card'

const styles = theme => ({
  root: {
    ...theme.base.section.root,
  },
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
    [theme.breakpoints.down('xs')]: {
      padding: theme.spacing(0),
      margin: [
        theme.spacing(0),
        theme.spacing(0),
        theme.spacing(2),
        theme.spacing(0),
      ],
    },
  },
})

const SuccessStories = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query CaseStudies {
          allCaseStudiesJson {
            nodes {
              title
              desc
              image
            }
          }
          allSectionsJson(filter: { id: { eq: "case-studies" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allCaseStudiesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const caseStudies = allCaseStudiesJson.nodes

        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />
            <Typography variant={'body1'} align={'center'} component={'span'}>
              {sectionDetails.desc}
            </Typography>

            <Grid
              container
              direction={'row'}
              justify={'space-between'}
              alignItems={'stretch'}
              className={classes.content}
            >
              <Grid item xs={12}>
                <Carousel>
                  {caseStudies.map((caseStudy, index) => (
                    <CarouselCard key={index} data={caseStudy} />
                  ))}
                </Carousel>
              </Grid>
            </Grid>
          </Container>
        )
      }}
    />
  )
}

SuccessStories.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(SuccessStories)
