import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import withStyles from '@material-ui/styles/withStyles'

import SectionHeader from '../components/sections-header/section-header'
import ProfileCard from '../components/profile-card/profile-card'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2, 0),
    marginTop: theme.spacing(2),
  },
})

const AboutUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query TeamMembers {
          allTeamMembersJson {
            nodes {
              name
              image
              roles
              skills
            }
          }
          allSectionsJson(filter: { id: { eq: "about-us" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allTeamMembersJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const teamMembers = allTeamMembersJson.nodes

        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />

            <Grid
              container
              direction={'row'}
              justify={'space-between'}
              alignItems={'stretch'}
              className={classes.content}
              spacing={4}
            >
              {teamMembers.map((teamMember, index) => (
                <Grid key={index} item sm={12} md={6} style={{ width: '100%' }}>
                  <ProfileCard
                    name={teamMember.name}
                    photo={teamMember.image}
                    roles={teamMember.roles}
                    skills={teamMember.skills}
                  />
                </Grid>
              ))}
            </Grid>
          </Container>
        )
      }}
    />
  )
}

AboutUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(AboutUs)
